html {
  font-size: 1rem;
  position: relative;
  min-height: 100%;
}

body {
  margin-bottom: 60px;
  font-family: system-ui, -apple-system, "Segoe UI", Roboto, "Helvetica Neue", Arial, sans-serif;
  line-height: 1.5;
}

.custom-card {
  box-shadow: 0 0.125rem 0.25rem rgba(0, 0, 0, 0.075);
  border-radius: 0.375rem;
  padding: 1rem;
  margin-bottom: 1rem;
  background: white;
  border: 1px solid rgb(229.5, 229.5, 229.5);
}
.custom-card:hover {
  box-shadow: 0 0.125rem 0.25rem rgba(0, 0, 0, 0.075);
  transform: translateY(-2px);
}
.custom-card__header {
  font-weight: bold;
  color: #0d6efd;
  margin-bottom: 0.5rem;
}
.custom-card__body {
  color: #6c757d;
}
.custom-card--success {
  border-left: 4px solid #198754;
}
.custom-card--warning {
  border-left: 4px solid #ffc107;
}
.custom-card--danger {
  border-left: 4px solid #dc3545;
}

.feature-list {
  list-style: none;
  padding: 0;
}
.feature-list li {
  padding: 0.5rem;
  border-bottom: 1px solid rgb(215.8369098712, 218.6566523605, 221.1630901288);
  transition: all 0.2s ease-in-out;
}
.feature-list li:last-child {
  border-bottom: none;
}
.feature-list li:hover {
  background-color: rgb(240.618852459, 246.3831967213, 254.881147541);
}
.feature-list li .icon {
  color: #0d6efd;
  margin-right: 0.5rem;
}

.scss-indicator {
  position: fixed;
  bottom: 100px;
  right: 10px;
  background: #cf649a;
  color: white;
  padding: 8px 16px;
  border-radius: 0.375rem;
  font-size: 12px;
  z-index: 9999;
}

.footer {
  position: absolute;
  bottom: 0;
  width: 100%;
  white-space: nowrap;
  line-height: 60px;
}

